}

.grid{
  /* border: 1px solid blue; */

  display: grid;

  width: 100%;
  /* place-items: center; */
  justify-content: flex-start;
  align-items: flex-start;
  grid-template-columns:100%;
  grid-gap:1rem;
  gap:1rem;

  /* margin: 2rem 0; */
  /* border: 2px solid blue; */
}

.productCol-conatiner{
  position: relative;
  width: 100%;

  display: flex;
  flex-direction: column;
  border-radius: 20px;
  background-color: white;
}

.productCol-conatiner>h1{
  padding: 1rem;
}

.productCol{
  width: 100%;
  display: grid;
  grid-template-columns: 5% 10% 10% 15% 10% 15% auto;
  flex-direction: column;
  /* border: 0.5px solid red; */
  
  padding: 0.2rem;
  align-items: center;
}

.navProdCol{
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.navProdCol:hover, .navProdCol:nth-child(even):hover{
  background-color: aquamarine;
  transition: 0.3s ease-in-out;
}

.productCol>h3{
  font-size: 1rem;
}

.productCol:nth-child(even){
