/* Product Page CSS */
.product-container{
  position: relative;
  width: 80%;
  min-height: 50vh;

  padding: 1rem;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  background-color: var(--formBgColor);
  border-radius: 20px;
}
.drop-down-container{
    position: relative;
    
    width: 100%;
    min-height: 1.5rem;

    background-color: white;
    border: 0.5px solid black;
}

.no-border-dropdown-container{
    /* padding: 0.4rem 0.8rem; */
    min-height: 2rem;

    /* display: flex;
    justify-content: flex-start;
    align-items: center; */

    /* background-color: var(--formBgColor); */

    border-radius: 10px;
    border: none;
}

.arrow-container{
    width: 2rem;
    height: 2rem;

    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */
}

.down-arrow{
    font-size: 1.2rem;
    color: var(--backgroundColor);
    /* border: 1px solid red; */
}

.drop-down-item{
    position: relative;
    width: 100%;
}

.selected-drop-down{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* display: grid; */
    /* flex-direction: row; */
    /* grid-template-columns: 95% 5%; */
    
    cursor: pointer;
}

.selected-drop-down>p{
    width: calc(100% - 2rem);
    /* border: 1px solid green; */
}

.drop-down-list-container{
    position: absolute;

    width: 100%;
    height: calc(5 * 2rem);

    background-color: white;
    border: 0.5px solid black;

    overflow-y: scroll;
    cursor: pointer;

    z-index: 10;
}

.no-border-dropdown-list{
    top:calc(2rem + 2px);
    border-radius: 5px;
    border: none;
}
.loader-container{
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100vh;

    background-color: black;
    opacity: 0.3;

    z-index: 10;
}

.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
    }
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
.path-options{
    font-size: 2rem;
    padding: 1rem 2rem;
    margin: 1rem 0;

    background-color: white;
    border-radius: 20px;
    cursor: pointer;
}

.header{
    width: 100%;
    height: auto;

    display: flex;
    justify-content: space-between;
}

.user-card-grid{
    display: grid;
    grid-template-columns: 48% 48%;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
}


.edit-btn{
    display: flex;
    justify-content: center;
    align-items: center;

    align-self: flex-end;

    position: absolute;
    top: 0.5rem;
    right: 0.5rem;

    width: 1.5rem;
    height: 1.5rem;

    padding: 0.1rem 0.3rem;
    font-size: 1rem;

    background-color: var(--formBgColor);
    border: none;
    box-shadow: 2px 2px black;
    cursor: pointer;

    z-index: 5;

    transition: 0.3s ease-in-out;
}

.edit-btn:hover{
    transition: 0.3s ease-in-out;
    background-color: #c6cec9;
}

.modal-backdrop{
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100vh;

    background-color: black;
    opacity: 0.3;

    z-index: 1;
}

.modal-container{
    position: fixed;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    z-index: 9;
}

.card-container{
    position: relative;
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    padding: 0.5rem;

    border-radius: 15px;
    border: 0.5px solid var(--backgroundColor);
}

.card-grid{
    display: grid;
    grid-template-columns: 48% 48%;
    grid-column-gap: 2rem;
    column-gap: 2rem;
}

.card-container>p>span{
    font-weight: bold;
}

.status-circle{
    width: 1.5rem;
    height: 1.5rem;

    align-self: flex-end;
    justify-self: flex-end;

    border-radius: 50%;
}

.card-row{
    width: 100%;
    min-height: 3vh;

    display: grid;
    grid-template-columns: 50% 50%;
    /* border: 1px solid red; */
}

.card-element{
    display: flex;
    flex-direction: column;

    /* height: 100%; */
    /* justify-self: center;
    align-self: center; */

    /* border: 1px solid blue; */
}

.card-grid-row{
    width: 100%;

    margin-bottom: 0.2rem;

    display: grid;
    grid-template-columns:3rem 54% calc(23% - 1.5rem) calc(23% - 1.5rem) ;

    /* justify-content: space-between; */
    align-items: center;

    border-bottom: 1px solid var(--backgroundColor);
    /* border: 1px solid red; */
}

.card-element>p{
    font-size: 1rem;
}

.card-grid-row>p{
    justify-self: center;
    align-self: center;
}

.card-element>h3, .card-grid-row>h3{
    font-size: 1rem;
}
:root{
  --backgroundColor:hsl(173, 58%, 39%);
  --formBgColor:#d8e2dc;
  --buttonGreen:#2a9d8f;
  --buttonRed:#e63946;
  --white:white;
  --inactive:#aec3b0;
}

*{
  box-sizing: border-box;
  padding:0;
  margin:0;
  font-family: 'Manrope', sans-serif;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  min-height: 100vh;

  /* background-color: #f2f4f3; */
  background-color: var(--backgroundColor);
  text-align: center;
  /* border: 1px solid red; */

  overflow-y: auto;
}


/*///////////////////////////////////////////////*/
/*/////////////////ScrollBar/////////////////////*/
/*///////////////////////////////////////////////*/
 
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  /* background: #e24545; */
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 2px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
} 

.footer-container{
    position:relative;
    /* left: 0; */
    /* bottom: 0; */

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    justify-self:end;
    align-self:flex-end;
    width: 100%;
    height: auto;

    padding: 1rem 0;

    /* border: 1px solid red; */

}
.ds-filter-container{
    width: 100%;
    height: 15vh;

    /* border: 1px solid blue; */

    display: flex;
    justify-content: center;
    align-items: center;
}

.ds-filter-dropdown-container{
    width: clamp(100px,10vw,10vw);

    margin: 0 1rem;
}

.ds-filter-btn{
    margin: 0.4rem;
    padding: 0.4rem 0.8rem;

    border-radius: 10px;
    background-color: var(--formBgColor);

    cursor: pointer;
}

.ds-filer-btn-active{
    background-color: transparent;
    outline: 2px solid var(--formBgColor);
    color: var(--formBgColor);
}

.ds-pagination-container{
    width: auto;
    height: auto;

    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 20px;
    background-color: var(--formBgColor);
    animation: onLoad 0.3s ease-in forwards;
}

@keyframes onLoad{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.ds-page-btn{
    margin: 0.4rem;
    padding: 0.4rem 0.8rem;
    cursor: pointer;

    transition: 0.3s ease-in-out;
}

.ds-page-inactive{
    color: var(--inactive);
}


.ds-page-btn:hover{
    color: var(--backgroundColor);
    transition: 0.3s ease-in-out;
}

.ds-page-inactive:hover{
    color: var(--inactive);
    cursor: default;
}

.ds-page-btn-active{
    color: var(--backgroundColor);
}

.ds-filter-data-container{
    width: 100%;
    min-height: 80vh;

    /* display: grid;
    grid-template-columns: 70% 30%; */
    display: flex;
    justify-content: center;
    align-items: flex-start;

    /* border: 1px solid red; */
}

.ds-new-box{
    width: 80vw;
    min-height: 20vh;

    background-color: var(--formBgColor);
    border-radius: 20px;

    padding: 2rem;

    /* display: flex;
    flex-direction: column; */
    display: grid;
    grid-template-columns: 50% 50%;
   
}

.ds-new-col{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
} 

.ds-col-prod-list{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    padding-left: 1rem;
}

.ds-col-prod-list>p{
   text-align: left;
}
.ds-col-prod-list>p>span,.ds-new-col>p>span{
    font-weight: bold;
}

.ds-content{
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* display: grid;
    grid-template-columns: 33% 33% 33%; */

    /* border: 1px solid red; */
}

.ds-card-content{
    position: relative;
    display: grid;
    grid-template-columns: 30% 30% 30%;
    grid-column-gap: 2rem;
    column-gap: 2rem;

    grid-row-gap: 2rem;

    row-gap: 2rem;

    /* border: 1px solid red; */
}

.ds-search-container{
    width: 90%;
    height: auto;

    background-color: var(--formBgColor);
    padding: 1rem;

    border-radius: 10px;

    /* border: 1px solid blue; */
}

.ds-slip-box{
    position: relative;

    width: 100%;
    height: 100%;
    min-height: 20vh;

    padding: 1rem;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;

    text-align: left;

    border-radius: 20px;
    background-color: var(--formBgColor);
    margin-bottom: 0.5rem;
}
.ds-slip-box>p{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;

    margin-bottom: 0.5rem;
}

.ds-slip-box>p>span{
    font-weight: bold;
}

.pdf-btn{
    width: auto;
    height: auto;

    align-self: center;
    justify-self: flex-end;

    padding: 1rem;
    background-color: var(--formBgColor);

    border-radius: 28px;
    border: 1px solid var(--backgroundColor);

    cursor: pointer;

    transition: 0.3s ease-in-out;
}

.pdf-btn:hover{
    background-color: var(--backgroundColor);
    color: var(--formBgColor);
    transition: 0.3s ease-in-out;
}

/* /////////////////////1200///////////////////// */
@media only screen and (max-width: 1200px ){
    .ds-card-content{
        position: relative;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-column-gap: 2rem;
        column-gap: 2rem;
    
        grid-row-gap: 2rem;
    
        row-gap: 2rem;

        padding: 0 2rem;
        
    
        /* border: 1px solid red; */
    }
}

/* /////////////////////768///////////////////// */
@media only screen and (max-width: 768px ){
    .ds-card-content{
        position: relative;
        display: grid;
        grid-template-columns: 100%;
        /* column-gap: 2rem; */
    
        grid-row-gap: 2rem;
    
        row-gap: 2rem;
    
        padding: 0 2rem;
        /* border: 1px solid red; */
    }
}
